.productPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 8rem;
}
.prodcutPageTop {
  display: flex;
  width: auto;
  gap: 1rem;
  justify-content: space-around;

  margin-bottom: 1rem;
}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: fit-content;
  width: 80%;
}

.products1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60vw;

  margin: 1rem 0;
}
.filterBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  width: 20%;
  margin: 1rem 0 0 0.8rem;
  background-color: white;
  border-radius: 5px;
  box-sizing: border-box;
  height: fit-content;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.329);
}

.priceFilter {
  display: flex;
  flex-direction: column;

  align-items: center;
}

.priceSlider {
  width: 90%;
}
.priceSlider .css-187mznn-MuiSlider-root {
  height: 3px !important;
  color: #414141 !important;
}

.priceSelector {
  display: flex;
  gap: 10px;
  align-items: center;
  width: 90%;
  margin: 10px 0;
}

.priceOption {
  width: 90%;
  height: 2rem;
}

.toText {
  font-size: 16px;
  font-weight: 400;
  color: #414141;
}
.filter_divider {
  width: 100%;
  height: 0.8px;
  margin: 1.5rem 0;
  background-color: #d7d5d2d4;
}
.menu_item {
  color: white !important;
  box-shadow: rgb(255, 255, 255) !important;
}

.categoriesFilter {
  width: 90%;
}
.categoryBox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 90%;
}
.category-link {
  display: flex;
  align-items: center;
  list-style: none;
  color: rgba(0, 0, 0, 0.61);
  font: 400 0.8vmax 'Roboto';
  margin: 0.4vmax;
  cursor: pointer;
}
.category-label {
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 10px;
}

.category-checkbox {
  width: 16px;
  height: 16px;
}

.category-checkbox:checked + .category-label::before {
  background-color: #414141;
}

.ratingsFilter {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.ratingsBox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 80%;
}
.paginationBox {
  display: flex;
  justify-content: center;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 2rem 0 2rem 0;
}
.page-item {
  background-color: rgb(255, 255, 255);
  list-style: none;
  border: 1px solid rgba(0, 0, 0, 0.178);
  padding: 1vmax 1.5vmax;
  transition: all 0.3s;
  cursor: pointer;
}

.page-item:first-child {
  border-radius: 5px 0 0 5px;
}

.page-item:last-child {
  border-radius: 0 5px 5px 0;
}
.page-link {
  text-decoration: none;
  font: 300 0.9vmax 'Roboto';
  color: rgb(2, 2, 2);
  transition: all 0.3s;
}

.page-item:hover {
  background-color: #414141;
}

.page-item:hover .page-link {
  color: rgb(234, 10, 10);
}

.pageItemActive {
  background-color: #ed1c24;
}

.pageLinkActive {
  color: white;
}

.css-187mznn-MuiSlider-root {
  color: #170f05 !important;

  height: 1.2px !important;
}
.css-eg0mwd-MuiSlider-thumb {
  width: 10px !important;
  height: 10px !important;
}

.css-ahj2mt-MuiTypography-root {
  text-align: center;
}

.categoryBox {
  padding: 0%;
}

.category-link:hover {
  color: tomato;
}

.css-1t2bqnt {
  color: #170f05 !important;
}

.css-exkjcx {
  color: #414141 !important;
}

.filterBox > fieldset {
  border: 1px solid rgba(0, 0, 0, 0.329);
}

@media screen and (max-width: 1100px) {
  .filterBox {
    width: 30vmax;
  }
  .page-link {
    font: 300 1.2vmax 'Roboto';
  }
  .products1 {
    width: 55vw;
  }
  .category-link {
    font: 400 1.3vmax 'Roboto';
  }
}

@media screen and (max-width: 800px) {
  .filterBox {
    width: 40vmax;
  }
  .products1 {
    width: 45vw;
  }

  .page-link {
    font: 300 1.5vmax 'Roboto';
  }
  .category-link {
    font: 400 1.6vmax 'Roboto';
  }
}

@media screen and (max-width: 600px) {
  .prodcutPageTop {
    flex-direction: column-reverse;
    align-items: center;
    margin-top: 10px;
  }
  .filterBox {
    width: 60%;
    margin: 1rem 0 0 0;
  }

  .products {
    width: 100%;
    margin: 1rem 0 0 0;
  }
  .products1 {
    width: 100%;
    margin: 1rem 0 0 0;
  }
}
