.cartText2 {
  font-size: 14px;
}
.cartHeading {
  font-size: 16px;
}

.additem {
  display: flex;
  align-items: center;
  justify-content: center;
}

.additem_decrease {
  margin-right: 8px;
  height: 24px;
  font-size: 14px;
  background-color: none;
  width: 0px;
  border-color: none;
  border-radius: 3px;
  color: black;
  margin-left: 5px;
}

.additem_increase {
  margin-left: 10px;
  height: 24px;
  background-color: none;
  width: 0px;
  border-color: none;
  border-radius: 3px;
  color: black;
}

.additem_increase:hover {
  color: #ed1c24;
  transition: transform 0.3s ease 0s;
  border-style: 2px solid;
}

.additem_decrease:hover {
  color: #ed1c24;
  transition: transform 0.3s ease 0s;
  border-style: 2px solid;
}

input[type='number'] {
  width: 32px;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
}

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
  appearance: textfield;
}
